body {
  margin: 0;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.gallery {
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  margin-top: 20px;
  display: grid;
}

.photo-card {
  background-color: #f3f3f3;
  border-radius: 4px;
  padding: 10px;
}

.photo-card img {
  width: 100%;
  height: auto;
  border-radius: 4px;
}

.info {
  text-align: center;
  margin-top: 10px;
}

.info-item {
  margin: 5px 0;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 16px;
  display: block;
}

.load-more:hover {
  background-color: #0056b3;
}

